  var DG = require('2gis-maps');
  var map = DG.map('map', {
    'center': [54.98, 82.89],
    'zoom': 13
}); 

  var nexrad = DG.tileLayer.wms("http://localhost:8080/geoserver/moscow/wms", {
    layers: 'moscow:border,moscow:poi',
    format: 'image/png',
    transparent: true,
    attribution: "Border"
  });   
 
  map.addLayer(nexrad);